import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  data;
  dist;
  tot;
  constructor( private _data:DataService) { }

  ngOnInit(): void {
    this._data.covid().subscribe((out)=>{this.data=out;this.dist=Object.keys(this.data.Kerala.districtData);console.log("Hai")})
    
  }
  total()
  {
    
    this.tot=this.data.Kerala.districtData.Alappuzha.deceased+this.data.Kerala.districtData.Ernakulam.deceased+this.data.Kerala.districtData.Idukki.deceased+this.data.Kerala.districtData.Kannur.deceased+this.data.Kerala.districtData.Kasaragod.deceased+this.data.Kerala.districtData.Kollam.deceased+this.data.Kerala.districtData.Kottayam.deceased+this.data.Kerala.districtData.Kozhikode.deceased+this.data.Kerala.districtData.Malappuram.deceased+this.data.Kerala.districtData.Palakkad.deceased+this.data.Kerala.districtData.Pathanamthitta.deceased+this.data.Kerala.districtData.Thiruvananthapuram.deceased+this.data.Kerala.districtData.Thrissur.deceased+this.data.Kerala.districtData.Wayanad.deceased;
    return this.tot;
  }

}
